.markdown {
  @apply text-lg leading-normal text-gray-700;
  & > * + *, & li > p + p {
    @apply mt-6;
  }
  & p {
    @apply mt-6;
  }
  & strong {
    @apply font-bold text-black;
  }
  & a {
    @apply font-semibold text-indigo-600 transition duration-150;
    &:hover {
      @apply text-indigo-400
    }
  }
  & strong a {
    @apply font-bold;
  }
  & h1 {
    @apply mt-10 mb-4 text-2xl font-bold leading-tight text-gray-800;
  }
  & h2 {
    @apply mt-10 mb-2 text-xl font-bold leading-tight text-gray-800;
  }
  & h3 {
    @apply mt-8 -mb-2 text-lg font-bold leading-tight text-gray-800;
  }
  & code {
    @apply inline px-1 font-mono text-sm text-gray-300;
  }
  & pre code {
    @apply block p-4 my-4 bg-indigo-900 rounded;
  }
  & blockquote {
    @apply pl-4 italic border-l-4 border-gray-400;
  }
  & ul, & ol {
    @apply pl-5;
    @screen sm {
      @apply pl-10;
    }
  }
  & ul {
    @apply list-disc;
  }
  & ol {
    @apply list-decimal;
  }
  & img {
    @apply mx-auto;
  }
  & hr {
    box-sizing: initial;
    height: 0;
    overflow: visible;
    @apply mx-24 my-12;
  }

  & table {
    @apply my-4 mx-auto divide-y divide-gray-200 /*min-w-full*/
  }
  & thead {
    @apply bg-gray-50
  }
  & th {
    @apply px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider
  }
  & tbody {
    @apply bg-white divide-y divide-gray-200
  }
  & td {
    @apply px-6 py-4 whitespace-nowrap text-sm
  }

  & caption {
    @apply py-3 text-sm text-gray-400;
    caption-side: bottom;
  }
}
