@tailwind base;

@tailwind components;

@tailwind utilities;



.browser {
	font-family: system, -apple-system, ".SFNSDisplay-Regular", "Helvetica Neue", "Lucida Grande", sans-serif;
	font-size: 14px;
	border-radius: 4px;
	border: 1px solid #C3C1C3;
	box-shadow: 0px 8px 32px #666666;
	/*width: 1224px;*/
	/*height: 768px;*/
}
.browser .chrome-top, .browser .chrome {
	background-color: #D4D2D4;
}
.browser .chrome-top {
	height: 48px;
}
.browser .chrome {
	height: 40px;
	box-sizing: border-box;
	padding-top: 6px;
	border-bottom: 1px solid #C3C1C3;
}
.browser .chrome input {
	display: block;
	width: 60%;
	height: 28px;
	margin-left: auto;
	margin-right: auto;
	line-height: 24px;
	padding-left: 4px;
	padding-right: 4px;
	border: 1px solid #C3C1C3;
	border-radius: 4px;
	color: #404040;
	font: inherit;
	text-align: center;
}
.browser .chrome-top .buttons {
	float: left;
	width: 60px;
	height: 28px;
	margin-left: 4px;
}
.browser .chrome-top .buttons .button {
	display: block;
	float: left;
	width: 12px;
	height: 12px;
	margin: 7px 3px 7px 3px;
	border: 1px solid black;
	border-radius: 50%;
}
.browser .chrome-top .buttons .button.close {
	background-color: white;
	border-color: #DE4643;
}
.browser .chrome-top .buttons .button.minimise {
	background-color: white;
	border-color: #DD9E33;
}
.browser .chrome-top .buttons .button.maximise {
	background-color: white;
	border-color: #26A934;
}
.browser .main {
	clear: both;
	height: 500px; /* 540 - 28 - 40*/
}
.browser .main > * {
	border-bottom-left-radius: 4px;
	border-bottom-right-radius: 4px;
}
.browser .main .content {
	padding: 8px;
}
.browser .main iframe {
	width: 133%;
	height: 133%;
	border: 0;
	-ms-zoom: 0.75;
	-moz-transform: scale(0.75);
	-moz-transform-origin: 0 0;
	-o-transform: scale(0.75);
	-o-transform-origin: 0 0;
	-webkit-transform: scale(0.75);
	-webkit-transform-origin: 0 0;
}

iframe.zoomout {
	width: 133%;
	height: 133%;
	border: 0;
	-ms-zoom: 0.75;
	-moz-transform: scale(0.75);
	-moz-transform-origin: 0 0;
	-o-transform: scale(0.75);
	-o-transform-origin: 0 0;
	-webkit-transform: scale(0.75);
	-webkit-transform-origin: 0 0;
}

